<template>
  <div class="film-box" style="height:100%">
    <div class="content" :style="{ minHeight: curHeight+'px'}" style="padding-top: 1rem;">
      <!--      导航-->
      <div class="hedeBox" style="position: fixed;left: 0;top: 0;width: 100%;z-index: 10;">
        <nav-head :title="title" ></nav-head>
      </div>
      <!--      header 头部-->
      <div class="header">
        <div class="first-tab">
          <div class="first-tab-left">
            <span :class="isBusiness == 1?'title-active':''" @click="businessBut(1)">我要买</span>
            <span :class="isBusiness == 2?'title-active':''" @click="businessBut(2)">我要卖</span>
          </div>
          <div class="first-tab-right" @click="toOrder()">
            <span style="margin-right: 0.2rem; color: #777985;font-size: .24rem;">历史记录</span>
            <img src="../../assets/image/capital/record.png" alt="" style="vertical-align: middle;">
          </div>
        </div>
        <!--第二栏-->
        <div class="second-tab">
        <!-- tabbar区域-->
          <div class="second-nav-box">
            <span @click="navClick(index)" :class="spanActive == index?'nav-tab-active':''"  v-for="(item,index) in legalCoinList" :key="index">{{item.type}}</span>
          </div>
        </div>
        <!--分割线-->
        <div class="dividing-line"></div>
      </div>
      <!--主要区域  "向他购买"  -->
      <div class="main">
        <div v-for="(item,index) in purchaseList" :key="index" class="list-item" :class="index%2==0?'deep':''">
          <div class="list-nav-box">
            <div class="list-nav-left">
              <span class="left-icon">东</span>
              <span style="margin-left: 0.27rem">{{item.name1}}</span>
            </div>
            <p>成交率 <span style="color: #13182D"> | </span> {{item.rate}}</p>
          </div>
          <div class="list-price-box">
            <div class="list-price-left">
              <span class="left-text">单价</span>
              <span v-show="isBusiness == 1" style="margin-left: 0.2rem;font-size: 0.36rem;color: #0FA478">￥{{item.price}}</span>
              <span v-show="isBusiness == 2" style="margin-left: 0.2rem;font-size: 0.36rem;color: #FA4C45">￥{{item.price}}</span>
            </div>
            <span @click="showBox1(item.id,item.price)" class="right-btn" v-show="isBusiness == 1">购买</span>
            <span @click="showBox2(item.id,item.price)" class="right-btn1" v-show="isBusiness == 2">出售</span>
          </div>
          <div class="list-num-box">
            <div class="list-num-left">
              <span class="left-text">数量</span>
              <span style="margin-left: 0.27rem;">{{item.number}} {{item.designation}}</span>
            </div>
          </div>
          <div class="list-limit-box">
            <div class="list-limit-left">
              <span class="left-text">限额</span>
              <span style="margin-left: 0.27rem;">￥{{item.min_price}}-{{item.max_price}} CNY</span>
            </div>
          </div>
        </div>
      </div>
    </div>
    <!-- 点击购买弹窗 -->
    <div class="posiBox"  v-if="isShow" @click="showBox()"></div>
    <div class="ventanal"  v-if="isShow">
      <div class="title">{{tipsTtile}}</div>
      <div class="isTips">
        <span class="num">单价: <span style="color:#3DD078">{{userPrice}}</span></span>
      </div>
      <div class="iptBox clear-fix">
        <div class="text fl">商户id</div>
        <input class="ipt fl"  disabled="disabled" type="number" v-model="purchaseCoinData.id" placeholder="请输入数量">
      </div>
      <div class="iptBox clear-fix">
        <div class="text fl">购买数量</div>
        <input class="ipt fl" type="number" @input="changeInput" v-model="num" placeholder="请输入数量">
      </div>
      <div class="iptBox clear-fix">
        <div class="text fl">总金额</div>
        <input class="ipt fl" type="number" @input="changeInput1" v-model="totalAmount" placeholder="请输入购买金额">
      </div>
      <!-- /买提交 -->
      <div class="subBox" @click="buySubmit()">买提交</div>
    </div>
    <!-- 点击出售弹窗 -->
    <div class="posiBox"  v-if="isShow2" @click="showBox()"></div>
    <div class="ventanal"  v-if="isShow2" style="height: 10rem;margin-top: -5rem;">
      <div class="title">{{tipsTtile}}</div>
      <div class="isTips">
        <span class="num">单价: <span style="color:#3DD078">{{userPrice}}</span></span>
        <!-- <span class="nums">投票数最多:<span style="color:#FA4C45">11</span></span> -->
      </div>
      <div class="iptBox clear-fix">
        <div class="text fl">商户id</div>
        <input class="ipt fl"  disabled="disabled" type="number" v-model="sellCoinData.id" placeholder="请输入数量">
      </div>
      <div class="iptBox clear-fix">
        <div class="text fl">支付宝账号</div>
        <input class="ipt fl" type="text"  v-model="sellCoinData.phone" placeholder="请输入支付宝账号">
      </div>
      <div class="iptBox clear-fix">
        <div class="text fl">收款人姓名</div>
        <input class="ipt fl" type="text" v-model="sellCoinData.pname" placeholder="请输入收款人姓名">
      </div>
      <div class="iptBox clear-fix">
        <div class="text fl">购买数量</div>
        <input class="ipt fl" type="number" @input="sellInput1" v-model="num" placeholder="请输入数量">
      </div>
      <div class="iptBox clear-fix">
        <div class="text fl">总金额</div>
        <input class="ipt fl" type="number" @input="sellInput2" v-model="totalAmount" placeholder="请输入购买金额">
      </div>
      <!-- /卖提交 -->
      <div class="subBox" @click="salesSubmit()">提交</div>
    </div>
  </div>
</template>

<script>
import Vue from 'vue'
import { Toast } from 'vant'
import { legalCoin, purchase, purchaseCoin, sellCoin } from '@/api/user'
import { objKeySort, sgin } from '@/utils/auth'
import NavHead from '@/components/head.vue'
Vue.use(Toast)
export default {
  components: { NavHead },
  data () {
    return {
      curHeight: '',
      title: '法币交易',
      spanActive: 0,
      isBusiness: 1, // 判断买卖
      legalCoinData: {
        u_id: ''
      }, // 法币币种种类参数
      legalCoinList: [], // 法币币种种类列表
      purchaseData: {
        u_id: '',
        coin: '',
        type: '',
        page: '1',
        showpage: '50'
      }, // 法币列表参数
      isShow: false,
      isShow2: false,
      tipsTtile: '默认标题', // 弹窗字段
      purchaseList: [], // 法币列表的集合
      // 法币购买对象
      purchaseCoinData: {
        u_id: '',
        num: '1',
        yuan: '1',
        id: '1'
      },
      // 法币卖出对象
      sellCoinData: {
        u_id: '',
        num: '1',
        yuan: '1',
        id: '1',
        pname: '',
        phone: ''
      },
      userPrice: '', // 单件
      num: '', // 数量
      totalAmount: '' // 总金额
    }
  },
  created () {
    this.legalCoinData.u_id = sessionStorage.getItem('u_id')
    this.purchaseData.u_id = sessionStorage.getItem('u_id')
    this.purchaseCoinData.u_id = sessionStorage.getItem('u_id')
    this.sellCoinData.u_id = sessionStorage.getItem('u_id')
    this.beforeMount()
    this.legalCoinFun()
  },
  methods: {
    // 卖提交
    salesSubmit () {
      var that = this
      if (that.sellCoinData.phone === '') {
        that.$toast({ message: '支付宝账号不能为空', duration: 1500 })
        return
      }
      if (that.sellCoinData.pname === '') {
        that.$toast({ message: '收款姓名不能为空', duration: 1500 })
        return
      }
      if (that.num === '') {
        that.$toast({ message: '购买数量不能为空', duration: 1500 })
        return
      }
      if (that.totalAmount === '') {
        that.$toast({ message: '总金额不能为空', duration: 1500 })
        // return
      }
      this.sellCoinFun()
    },
    // 法币出售
    sellCoinFun () {
      var that = this
      that.sellCoinData.yuan = that.totalAmount
      that.sellCoinData.num = that.num
      var data = objKeySort(that.sellCoinData)
      var token = sgin(data)
      this.sellCoinData.token = token
      sellCoin(that.sellCoinData).then(res => {
        console.log(res, that, '法币出售')
        if (res.code === 0) {
          that.$toast({ message: res.data.msg, duration: 1500 })
          that.isShow2 = false
          that.sellCoinData.yuan = ''
          that.sellCoinData.pname = ''
          that.sellCoinData.phone = ''
          that.sellCoinData.num = ''
          delete that.sellCoinData.token
        }
      }).catch((err) => {
        that.$toast({ message: err, duration: 1500 })
        that.isShow = false
        that.sellCoinData.yuan = ''
        that.sellCoinData.num = ''
        that.sellCoinData.pname = ''
        that.sellCoinData.phone = ''
        delete that.sellCoinData.token
      })
    },
    // 买提交
    buySubmit () {
      var that = this
      if (this.num === '') {
        that.$toast({ message: '购买数量不能为空', duration: 1500 })
        return
      }
      if (this.totalAmount === '') {
        that.$toast({ message: '总金额不能为空', duration: 1500 })
        return
      }
      this.purchaseCoinFun()
    },
    // 法币购买
    purchaseCoinFun () {
      var that = this
      that.purchaseCoinData.yuan = that.totalAmount
      that.purchaseCoinData.num = that.num
      var data = objKeySort(that.purchaseCoinData)
      var token = sgin(data)
      this.purchaseCoinData.token = token
      purchaseCoin(that.purchaseCoinData).then(res => {
        console.log(res, that, '法币购买')
        if (res.code === 0) {
          that.$toast({ message: res.data.msg, duration: 1500 })
          that.isShow = false
          that.purchaseCoinData.yuan = ''
          that.purchaseCoinData.num = ''
          delete that.purchaseCoinData.token
        }
      }).catch((err) => {
        that.$toast({ message: err, duration: 1500 })
        that.isShow = false
        that.purchaseCoinData.yuan = ''
        that.purchaseCoinData.num = ''
        delete that.purchaseCoinData.token
      })
    },
    // 单买
    sellInput1 () {
      var that = this
      this.$nextTick(() => {
        this.totalAmount = Number(that.userPrice) * Number(that.num)
      })
    },
    sellInput2 () {
      var that = this
      console.log(that.totalAmount, '总金额')
      this.$nextTick(() => {
        console.log(that.totalAmount, that.isAmount)
        this.num = Number(that.totalAmount) / Number(that.userPrice)
      })
    },
    // 单买
    changeInput () {
      var that = this
      this.$nextTick(() => {
        this.totalAmount = Number(that.userPrice) * Number(that.num)
      })
    },
    // 单买修改金额
    changeInput1 () {
      var that = this
      console.log(that.totalAmount, '总金额')
      this.$nextTick(() => {
        console.log(that.totalAmount, that.isAmount)
        this.num = Number(that.totalAmount) / Number(that.userPrice)
      })
    },
    showBox (type) {
      this.isShow = false
      this.isShow2 = false
    },
    showBox1 (id, price) {
      this.tipsTtile = '购买'
      // item.id,item.price
      this.userPrice = price
      this.purchaseCoinData.id = id
      this.isShow = !this.isShow
    },
    showBox2 (id, price) {
      this.tipsTtile = '出售'
      this.userPrice = price
      this.sellCoinData.id = id
      this.isShow2 = !this.isShow2
    },
    businessBut (type) {
      var that = this
      this.isBusiness = type

      // that.spanActive = index
      that.purchaseData.coin = that.legalCoinList[that.spanActive].type
      that.purchaseData.type = that.isBusiness
      that.purchaseFun() // 调用列表
    },
    // 法币列表
    purchaseFun () {
      var that = this
      var data = objKeySort(this.purchaseData)
      var token = sgin(data)
      this.purchaseData.token = token
      purchase(that.purchaseData).then(res => {
        console.log(res, that, '法币选择列表')
        if (res.code === 0) {
          that.purchaseList = res.data.data
          delete that.purchaseData.token
        }
      }).catch((err) => {
        that.$toast({ message: err, duration: 1500 })
        delete that.purchaseData.token
      })
    },
    // 点击币种导航
    navClick (index) {
      console.log('index,', index)
      var that = this
      that.spanActive = index
      that.purchaseData.coin = that.legalCoinList[index].type
      that.purchaseData.type = that.isBusiness
      that.purchaseFun() // 调用列表
    },
    // 法币币种
    legalCoinFun () {
      var that = this
      var data = objKeySort(this.legalCoinData)
      var token = sgin(data)
      this.legalCoinData.token = token
      legalCoin(that.legalCoinData).then(res => {
        console.log(res, that, '法币种类')
        if (res.code === 0) {
          that.legalCoinList = res.data.result
          that.purchaseData.coin = res.data.result[0].type
          that.purchaseData.type = that.isBusiness
          that.purchaseFun() // 调用列表
          // that.$toast({ message: '参与成功', duration: 1500 })
          delete that.legalCoinData.token
        }
      }).catch((err) => {
        that.$toast({ message: err, duration: 1500 })
        delete that.legalCoinData.token
      })
    },
    beforeMount (height) {
      var h = document.documentElement.clientHeight || document.body.clientHeight
      this.curHeight = h
      console.log(this.curHeight, 'this.curHeight')
    },
    toOrder () {
      this.$router.push({
        path: '/currency/record',
        query: {
          // types: '法币'
        }
      })
    }
  }
}
</script>
